import React from 'react'

export default function Preloader({ setIsLoading, loading }) {

    const selector = DOMelement => {
      return document.querySelector(DOMelement);
    }

    const loadPage = () => {
        let ovrl = selector('.preloader')
        let stat = selector('.percent')
        let img = document.images
        let c = 0
        let tot = img.length
            
        const doneLoading = () => {
            setIsLoading(!loading)
            setTimeout(() => {
                ovrl.classList.add('done')
            }, 500)
            setTimeout(() => {ovrl.style.zIndex = -1}, 4200)
        }

        const imgLoaded = () => {
            c += 1
            let perc = ((100 / tot * c) << 0) - 1 + '%'

            stat.innerHTML = perc
            if (c === tot) return doneLoading()
        }

        
        for (let i = 0; i < tot; i++) {
            let tImg = new Image()
            tImg.onload = imgLoaded
            tImg.onerror = imgLoaded
            tImg.src = img[i].src
        }
    }
    

    document.addEventListener('DOMContentLoaded', loadPage, false)

    return (
        <></>
    )
};