import React, { useEffect } from 'react'
import { TextLinesReveal } from '../../utils/textLinesReveal'
import gsap from 'gsap'

const TextAnimation = ({ loading }) => {

    useEffect(() => {

        let DOM = {}

        gsap.utils.toArray('._text-fill .back').forEach(text => {

            gsap.to(text, {
                height: '0',
                markers: true,
                scrollTrigger: {
                    trigger: text,
                    start: '-=600',
                    end: '+=800',
                    scrub: true
                }
            })
        })

        gsap.utils.toArray('._text-fill-horizontal .back').forEach(text => {

          gsap.to(text, {
              width: '0',
              markers: true,
              scrollTrigger: {
                  trigger: text,
                  start: '-=600',
                  end: '+=800',
                  scrub: true
              }
          })
        })

        gsap.utils.toArray('._line-bar').forEach(line => {

          gsap.to(line, {
              width: '100%',
              scrollTrigger: {
                  trigger: line,
                  start: '-=600',
                  end: '+=800',
                  scrub: true
              }
          })
        })

        DOM.textContent = {
          textOnLoad: document.querySelectorAll('._text-on-load'),
          textOnScroll: document.querySelectorAll('._text-on-scroll')
        };

        const textLinesRevealOnLoad = new TextLinesReveal(
            [...DOM.textContent.textOnLoad]
        );
        const textLinesRevealOnScroll = new TextLinesReveal(
            [...DOM.textContent.textOnScroll]
        );

        const showContentOnLoad = () => {
          textLinesRevealOnLoad.in();
        };

        const hideContentOnLoad = () => {
          textLinesRevealOnLoad.out();
        };

        const showContentOnScroll = () => {
            textLinesRevealOnScroll.inScroll();
        };
  
        // const hideContentOnScroll = () => {
        //     textLinesRevealOnScroll.outScroll();
        // };

        hideContentOnLoad()
        if(!loading) {
          showContentOnLoad()
        }

        //hideContentOnScroll()
        if(!loading) {
          showContentOnScroll()
        }
        
    }, [loading])

    return (
        <div>
            
        </div>
    )
}

export default TextAnimation
